export default {
  props: {
    searchIcon: String,
    save: {
      type: Boolean,
      default: true,
    },
  },
  render() {
    return (
      <div>
        <ElButton type="text" onClick={() => this.$emit("reset")}>
          <i class="iconfont jkd_icon_sync " /> 
        </ElButton>
        {this.save ? (
          <ElButton
            type="text"
            type="text"
            onClick={() => this.$emit("submit", "saveAndSearch")}
          >
            <i class="iconfont jkd_icon_save" /> 
          </ElButton>
        ) : (
          ""
        )}
        <ElButton
          size="mini"
          type="primary"
          icon={this.searchIcon}
          onClick={() => this.$emit("search")}
        >
          
        </ElButton>
      </div>
    );
  },
};
